import React from 'react'
import { Link } from 'gatsby'
import { isValidHex } from './../../utils/utils'
import './index.scss'

const ProductHero = ({ hero }) => {
  const { title, text, image, cta } = hero[0]

  const getColor = () => {
    let bc = '0a84ff'
    if (typeof URLSearchParams === 'function') {
      const urlParams = new URLSearchParams(window.location.search)
      if (urlParams.has('button') && isValidHex(urlParams.get('button'))) {
        bc = urlParams.get('button');
      }
    }
    console.log(bc)
    return `#${bc}`
  }

  return (
    <div className="product-hero">
      <header>
        <div className="product-hero__content">
          <h1>{title}</h1>
          <p>{text}</p>
          <Link to="/">
            {console.log('rendering')}
            <button style={{ backgroundColor: getColor() }}>{cta}</button>
          </Link>
        </div>
        <div className="product-hero__image">
          <img src={image.publicURL} alt={text} width="100%" height="auto" />
        </div>
      </header>
    </div>
  )
}

export default ProductHero
